.section-newsletter {
  background-image: url(../images/bg-newsletter.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 4rem;
  padding-bottom: 4rem;
  position: relative;
  form {
    .form-control {
      height: 2.5rem;
      &:focus {
        color: $text-gray;
        background-color: $white;
        border-color: $yellow;
        outline: 0;
        box-shadow: 0 0 0 0.25rem rgba($color: $yellow, $alpha: 0.25);
      }
    }
    .form-check-input{
      margin-bottom: 1rem;
      &:checked {
        background-color: $yellow;
        border-color: $yellow;
      }
      &:focus {
        border-color: $yellow;
        outline: 0;
        box-shadow: 0 0 0 0.25rem rgba($color: $yellow, $alpha: 0.25);
      }
    }
    .form-check-label{
      a {
        color: $white;
        text-decoration: none;
        transition: all 0.3s;
        &:hover {
          color: $yellow;
        }
      }
    }
    #btn-newsletter {
      height: 2.5rem;
      padding: 0.5rem 0;
    }
  }
}
.placaNewsletter {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../images/bg-newsletter.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  p {
    font-size: 1.5rem;
    font-weight: bold;
    color: $white;
  }
}