#navbar {
  display: none;
  @media (min-width: 1200px) {
    display: block;
  }
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 1030;
  transition: all 0.3s;
  .navbar {
    background-color: transparent;
    transition: all 0.3s;
    @media (max-width: 1199.98px) {
      max-width: 100%;
    }
    a {
      font-weight: 400;
      font-size: .8rem;
    }
    .navbar-brand {
      padding-top: 5px;
      padding-bottom: 5px;
      .logo-navbar {
        height: 80px;
      }
    }
    .navbar-nav {
      width: 40%;
      &.navbar-nav-left {
        justify-content: flex-end;
      }
      &.navbar-nav-center {
        width: 20%;
        justify-content: center;
      }
      &.navbar-nav-right {
        justify-content: flex-start;
      }
      .nav-item {
        padding-left: 1.3rem;
        padding-right: 1.3rem;
        width: 33.33%;
        @media (min-width: 1400px) {
          padding-left: 2rem;
          padding-right: 2rem;
        }
        .nav-link {
          color: $white;
          font-weight: 600;
          border-bottom: 2px solid transparent;
          text-align: center;
          &.active:before {
            left: 0;
            right: 0;
            background: $yellow;
          }
        }
        &:hover {
          .nav-link:before {
            background: $yellow;
          }
        }
      }
    }
  }
}

#navbar-mobile {
  background-color: $blue;
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: 60px;
  z-index: 1030;
  @media (min-width: 1200px) {
    display: none;
  }
  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    & > div {
      width: 33.33%;
    }
    .img-logo {
      height: 50px;
    }
    .btn-menu {
      background: transparent;
      border: none;
      img {
        height: 20px;
      }
    }
  }
  .menu-open {
    position: fixed;
    top: 0;
    left: -100%;
    width: 70%;
    background-color: $blue;
    height: 100%;
    overflow-y: auto;
    padding: 1rem 1rem 3rem 1rem;
    z-index: 1030;
    transition: all .7s;
    .icon-close {
      text-align: end;
      img  {
        width: 35px;
      }
    }
    .contenedor-menu {
      margin-top: 5rem;
      .title-menu {
        font-size: 3rem;
        color: $white;
        font-weight: bold;
        text-align: center;
      }
      .list-menu {
        padding-left: 0;
        list-style: none;
        li {
          border-bottom: 1px solid #e2cdcd;
          padding: 1rem;
          &:nth-child(1) {
            border-top: none;
          }
          a {
            text-decoration: none;
            color: $white;
            font-weight: 500;
            font-size: 1.3rem;
            &:hover {
              color: $yellow;
            }
            &:focus {
              color: $yellow;
            }
          }
          .notificacion {
            display: inline-block;
            font-size: 1rem;
            font-weight: 700;
            line-height: 1;
            color: $white;
            text-align: center;
            white-space: nowrap;
            vertical-align: baseline;
            border-radius: 50%;
            background-color: $yellow;
            padding: .6rem .8rem;
            margin-left: .8rem;
          }
        }
      }
      .redes-menu {
        ul {
          display: flex;
          justify-content: center;
          list-style: none;
          padding: 0;
          li {
            padding: 0 1rem;
            a {
              img {
                width: 3rem;
              }
            }
          }
        }
      }
    }
  }
}
    