html, body {
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
  color: $text-gray;
  font-size: 10px;
  @media (min-width: 425px) {
    font-size: 12px;
  }
  @media (min-width: 768px) {
    font-size: 14px;
  }
  @media (min-width: 1200px) {
    font-size: 16px;
  }
}
.bold {
  font-weight: bold !important;
}
.text-yellow {
  color: $yellow !important;
}
.content-title {
  display: flex;
  img {
    width: 3rem;
    height: 3rem;
    margin-right: 0.5rem;
  }
}
.title {
  font-size: 2rem;
  font-weight: 300;
  color: white;
  text-align: center;
}
.redes-blue {
  text-decoration: none;
  .fa-stack {
    font-size: 1.2rem;
    .icon-bg {
      color: $blue;
      transform: rotate(45deg);
      transition: all 0.3s;
    }
    .icon-red {
      color: $white;
      transition: all 0.3s;
    }
  }
  &:hover {
    .icon-bg {
      color: $white;
    }
    .icon-red {
      color: $blue;
    }
  }
}
.redes-yellow {
  text-decoration: none;
  .fa-stack {
    font-size: 1.2rem;
    .icon-bg {
      color: $yellow;
      transform: rotate(45deg);
      transition: all 0.3s;
    }
    .icon-red {
      color: $white;
      transition: all 0.3s;
    }
  }
  &:hover {
    .icon-bg {
      color: $white;
    }
    .icon-red {
      color: $yellow;
    }
  }
}