.section-footer1 {
  background-color: $white;
  padding-top: 3rem;
  padding-bottom: 3rem;
  h5 {
    color: $blue;
    font-weight: bold;
  }
  p {
    color: $text-gray;
    font-weight: 200;
  }
  ul {
    padding-left: 0;
  }
  li {
    list-style: none;
    margin-bottom: .3rem;
    .items-menu {
      color: $text-gray;
      font-weight: 200;
      text-decoration: none;
      &:hover {
        color: $yellow;
      }
    }
  }
  small {
    color: $text-gray;
  }
  .dropdown-menu {
    min-width: 145px !important;
    padding: 0 !important;
    border-radius: 1.25rem !important;
    border: none;
    margin-bottom: -3px !important;
    li {
      margin-bottom: 0 !important;
    }
  }
  .dropdown-item {
    color: $text-gray;
    background-color: $white !important;
    &:hover {
      color: $text-gray;
      background-color: $yellow !important;
    }
  }
  .btn-idiomas {
    color: $text-gray;
    border-radius: 1.2em;
    background-color: $white;
    text-align: left;
    max-width: 145px;
    .arrow-down {
      width: 0.8rem;
    }
    &.show {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
  }
  .logo-footer {
    width: 60%;
    @media (max-width: 767.98px) {
      width: 50%;
      max-width: 400px;
    }
  }
}
.section-mobil-footer {
  background-color: $white;
  color: $text-gray;
  padding: 2rem;
  .accordion-button {
    color: $text-gray;
    background-color: $white;
    font-weight: bold;
    font-size: 1.1rem;
    &:focus {
      border-color: $white;
      box-shadow: none;
    }
  }
  .accordion-body {
    background-color: $white;
    ul {
      list-style: none;
      color: $blue;
      padding-left: 0;
      a {
        text-decoration: none;
        color: $text-gray;
        &.hover {
          color: $yellow;
        }
      }
    }
  }
  .inf-oneup {
    padding: 1.5rem;
    h5 {
      font-size: 1.1rem;
      font-weight: bold;
    }
  }
}
.section-footer2 {
  background-color: $yellow;
  padding: .5rem;
  p {
    color: $text-gray;
    font-weight: 200;
  }
}
.ventanaPeq {
  width: 30vh;
  height: 60px;
  position: fixed;
  bottom: 0;
  right: 0;
  background-color: $text-gray;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (min-width: 768px) {
    width: 320px;
  }
  .close-reservar-peq {
    width: 20px;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
  }
  .img-rombo-reserva {
    width: 50px;
    margin-right: 1rem;
  }
  a {
    color: $yellow;
    font-size: 1.2rem;
    text-decoration: none;
    font-weight: bold;
  }
}
.ventanaGde {
  width: 30vh;
  height: 280px;
  position: fixed;
  bottom: 0;
  right: 0;
  background-color: $text-gray;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @media (min-width: 768px) {
    width: 320px;
  }
  .close-reservar {
    width: 20px;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
  }
  .img-rombo-reserva {
    width: 60%;
  }
  a {
    color: $yellow;
    font-size: 1.2rem;
    text-decoration: none;
    font-weight: bold;
  }
}